const cartData = localStorage.getItem("cart");
const cartexists = JSON.parse(cartData);

const INIT_STATE = { carts: cartexists, user: null };

const cartReducer = (state = INIT_STATE, action) => {
  switch (action.type) {
    case "ADD_USER":
      return {
        ...state,
        user: action.payload.data,
      };

    case "ADD_CART":
      if (!state.carts || state.carts.length === 0) {
        // If the cart is empty, null, or undefined, add the new item
        const updatedCarts = [
          {
            ...action.payload,
            quantity: 1,
            totalPrice: parseFloat(action.payload.price),
          },
        ];

        // Update localStorage after adding an item to the cart
        localStorage.setItem("cart", JSON.stringify(updatedCarts));

        return {
          ...state,
          carts: updatedCarts,
        };
      } else {
        const existingItem = state.carts.find(
          (el) => el._id === action.payload._id
        );

        if (existingItem) {
          // If the item already exists in the cart, increase its quantity
          const updatedCarts = state.carts.map((el) => {
            if (el._id === action.payload._id) {
              return {
                ...el,
                quantity: el.quantity + 1,
                totalPrice: el.totalPrice + parseFloat(action.payload.price),
              };
            }
            return el;
          });

          // Update localStorage after modifying the cart
          localStorage.setItem("cart", JSON.stringify(updatedCarts));

          return {
            ...state,
            carts: updatedCarts,
          };
        } else {
          // If the item doesn't exist in the cart, add it with a quantity of 1
          const updatedCarts = [
            ...state.carts,
            {
              ...action.payload,
              quantity: 1,
              totalPrice: parseFloat(action.payload.price),
            },
          ];

          // Update localStorage after adding an item to the cart
          localStorage.setItem("cart", JSON.stringify(updatedCarts));

          return {
            ...state,
            carts: updatedCarts,
          };
        }
      }

    case "RMV_CART":
      const { _id, quantity, price } = action.payload;

      if (quantity === 1) {
        const filteredCarts = state.carts.filter((el) => el._id !== _id);

        // Update localStorage after removing an item from the cart
        localStorage.setItem("cart", JSON.stringify(filteredCarts));

        return {
          ...state,
          carts: filteredCarts,
        };
      } else {
        const updatedCarts = state.carts.map((el) => {
          if (el._id === _id) {
            return {
              ...el,
              quantity: el.quantity - 1,
              totalPrice: el.totalPrice - price,
            };
          }
          return el;
        });

        // Update localStorage after modifying the cart
        localStorage.setItem("cart", JSON.stringify(updatedCarts));

        return {
          ...state,
          carts: updatedCarts,
        };
      }

    case "DLT_CART":
      const deleteId = action.payload._id;
      const filteredCarts = state.carts.filter((el) => el._id !== deleteId);

      // Update localStorage after deleting an item from the cart
      localStorage.setItem("cart", JSON.stringify(filteredCarts));

      return {
        ...state,
        carts: filteredCarts,
      };

    case "MERGE_CART_ITEMS":
      console.log(action.payload, "merge reducer started");
      const mergedItems = action.payload.map((item) => ({
        ...item.product,
        quantity: item.quantity,
        totalPrice: item.quantity * item.product.price,
      }));
      console.log("to be mergedItems == ", mergedItems, "||");

      // const updatedItems = [...state.carts.map((item) => ({ ...item }))];
      // console.log("cart state before merge ==", updatedItems, "||");
      // mergedItems.forEach((mergedItem) => {
      //   const existingIndex = updatedItems.findIndex(
      //     (item) => item._id === mergedItem._id
      //   );
      //   console.log(existingIndex, "exisiting index");
      //   if (existingIndex !== -1) {
      //     updatedItems[existingIndex].quantity += mergedItem.quantity;
      //     updatedItems[existingIndex].totalPrice += mergedItem.totalPrice;
      //     console.log(updatedItems[existingIndex].quantity, "quantity updated");
      //   } else {
      //     updatedItems.push(mergedItem);
      //     console.log("no match items , pushed directly");
      //   }
      // });
      console.log(mergedItems, "cart");
      return {
        ...state,
        carts: mergedItems,
      };

    case "CLEAR_CART_AND_USER":
      return {
        carts: [],
        user: null,
      };

    default:
      return state;
  }
};

export default cartReducer;
