import React, { useEffect, useState } from "react";
import Header from "../components/Header";
import Footer from "../components/Footer";
import { Link } from "react-router-dom";
import { Userslist } from "../components/Userstest";
import { useDispatch, useSelector } from "react-redux";
import jwtDecode from "jwt-decode";

const Checkout = () => {
  const getdata = useSelector((state) => state.cartReducer.carts);
  const [price, setPrice] = useState(0);
  const [discount, setDiscount] = useState("");
  const [discountedPrice, setDiscountedPrice] = useState("");
  const [couponCode, setCouponCode] = useState("");
  const [isCouponCodeValid, setIsCouponCodeValid] = useState(false);
  const [error, seterror] = useState("");
  const [appliedcoupon, setappliedcoupon] = useState("");
  const token = localStorage.getItem("token");
  const [loggedinuser, setloggedinuser] = useState("");
  useEffect(() => {
    if (token) {
      const decoded = jwtDecode(token);
      const loggedinuser = decoded._id;
      console.log(loggedinuser, "loggedinuser");
      setloggedinuser(loggedinuser);
    }
  }, []);
  const users = Userslist;
  const promocodes = users[0].promocode;

  // count total price of the cart
  const total = () => {
    let totalPrice = 0;
    getdata?.forEach((item) => {
      totalPrice += parseFloat(item.price * item.quantity);
    });
    setPrice(totalPrice);
  };

  useEffect(() => {
    total();
  }, [getdata]);

  useEffect(() => {
    let grandTotal = parseFloat(price);
    if (isCouponCodeValid) {
      const discountAmount = (discount / 100) * price;
      setDiscountedPrice((grandTotal - discountAmount).toFixed(3));
    } else {
      setDiscountedPrice(grandTotal);
    }
  }, [price, isCouponCodeValid, discount]);

  const checkPromoCode = (event) => {
    // console.log("promo")
    event.preventDefault();
    setappliedcoupon("");
    seterror("");
    const isPromoCodeValid = promocodes.find((el) => el.code === couponCode);
    if (isPromoCodeValid) {
      setIsCouponCodeValid(true);
      setDiscount(isPromoCodeValid.discount);
      setappliedcoupon(isPromoCodeValid.code);
    } else {
      seterror("Coupon not valid");
    }
  };
  const removeCouponCode = () => {
    // let grandTotal = parseFloat(price);
    // setDiscountedPrice(grandTotal.toFixed(3));
    setIsCouponCodeValid(false);
    setDiscount("");
    setappliedcoupon("");
    setCouponCode("");
  };

  useEffect(() => {
    console.log("remove", discount, isCouponCodeValid, appliedcoupon);
  }, [discount, isCouponCodeValid, appliedcoupon]);

  // accordian
  const [loginVisible, setLoginVisible] = useState(false);
  const [couponVisible, setCouponVisible] = useState(false);

  const toggleLogin = () => {
    setLoginVisible(!loginVisible);
  };

  const toggleCoupon = () => {
    setCouponVisible(!couponVisible);
  };

  return (
    <div>
      <Header />
      {/* breadcrumb-area start */}
      <div className="cart breadcrumb-area section-ptb">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h2 className="breadcrumb-title">Checkout</h2>
              {/* breadcrumb-list start */}
              <ul className="breadcrumb-list">
                <li className="breadcrumb-item">
                  <Link to="/">Home</Link>
                </li>
                <li className="breadcrumb-item active">Checkout</li>
              </ul>
              {/* breadcrumb-list end */}
            </div>
          </div>
        </div>
      </div>
      {/* breadcrumb-area end */}
      {/* main-content-wrap start */}
      <div className="main-content-wrap section-ptb checkout-page">
        <div className="container">
          <div className="row">
            <div className="col">
              <div className="coupon-area">
                {/* coupon-accordion start */}
                {loggedinuser ? (
                  ""
                ) : (
                  <div className="coupon-accordion">
                    <h3 onClick={toggleLogin}>
                      Returning customer?{" "}
                      <span className="coupon" id="showlogin">
                        Click here to login
                      </span>
                    </h3>
                    <div
                      className={`coupon-content ${loginVisible ? "open" : ""}`}
                      id="checkout-login"
                    >
                      <div className="coupon-info">
                        <p>
                          If you have shopped with us before, please enter your
                          details in the boxes below. If you are a new customer,
                          please proceed to the Billing &amp; Shipping section.
                        </p>
                        <form action="#">
                          <p className="coupon-input form-row-first">
                            <label>
                              Username or email{" "}
                              <span className="required">*</span>
                            </label>
                            <input type="text" name="email" />
                          </p>
                          <p className="coupon-input form-row-last">
                            <label>
                              password <span className="required">*</span>
                            </label>
                            <input
                              type="password"
                              autoComplete="off"
                              name="password"
                            />
                          </p>
                          <div className="clear" />
                          <p>
                            <button
                              type="submit"
                              className="button-login btn"
                              name="login"
                              value="Login"
                            >
                              Login
                            </button>
                            <label className="remember">
                              <input type="checkbox" defaultValue={1} />
                              <span>Remember</span>
                            </label>
                          </p>
                          <p className="lost-password">
                            <Link to="/forgotpassword">
                              Lost your password?
                            </Link>
                          </p>
                        </form>
                      </div>
                    </div>
                  </div>
                )}

                {/* coupon-accordion end */}
                {/* coupon-accordion start */}
                {loggedinuser && (
                  <div className="coupon-accordion">
                    <h3 onClick={toggleCoupon}>
                      Have a coupon?{" "}
                      <span className="coupon" id="showcoupon">
                        Click here to enter your code
                      </span>
                    </h3>
                    <div
                      className={`coupon-content ${
                        couponVisible ? "open" : ""
                      }`}
                      id="checkout-coupon"
                    >
                      <div className="coupon-info">
                        <form
                          onSubmit={checkPromoCode}
                          className="discount d-flex align-items-center gap-1 mb-3"
                        >
                          <div className="">
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Coupon Code"
                              aria-label="Recipient's username"
                              aria-describedby="basic-addon2"
                              value={couponCode}
                              onChange={(e) => setCouponCode(e.target.value)}
                            />
                          </div>
                          <button
                            type="submit"
                            className="btn button-apply-coupon"
                            name="apply_coupon"
                            value="Apply coupon"
                          >
                            Apply
                          </button>
                        </form>
                        {appliedcoupon && (
                          <div className="appliedcoupon d-flex justify-content-between align-items-center">
                            <b className="text-success"> {appliedcoupon}</b>
                            <i
                              onClick={() => (
                                removeCouponCode(),
                                setIsCouponCodeValid(false),
                                setDiscount(""),
                                setappliedcoupon(""),
                                setCouponCode("")
                              )}
                              className="fa-solid fa-xmark"
                            ></i>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                )}
                {/* coupon-accordion end */}
              </div>
            </div>
          </div>
          {/* checkout-details-wrapper start */}
          <div className="checkout-details-wrapper">
            <div className="row">
              <div className="col-lg-6 col-md-6">
                {/* billing-details-wrap start */}
                <div className="billing-details-wrap">
                  <form action="#">
                    <h3 className="shoping-checkboxt-title">Billing Details</h3>
                    <div className="row">
                      <div className="col-lg-6">
                        <p className="single-form-row">
                          <label>
                            First name <span className="required">*</span>
                          </label>
                          <input type="text" name="First name" />
                        </p>
                      </div>
                      <div className="col-lg-6">
                        <p className="single-form-row">
                          <label>
                            Username or email{" "}
                            <span className="required">*</span>
                          </label>
                          <input type="text" name="Last name" />
                        </p>
                      </div>
                      <div className="col-lg-12">
                        <p className="single-form-row">
                          <label>Company name</label>
                          <input type="text" name="email" />
                        </p>
                      </div>
                      <div className="col-lg-12">
                        <div className="single-form-row">
                          <label>
                            Country <span className="required">*</span>
                          </label>
                          <div className="nice-select wide">
                            <select>
                              <option>Select Country...</option>
                              <option>Albania</option>
                              <option>Angola</option>
                              <option>Argentina</option>
                              <option>Austria</option>
                              <option>Azerbaijan</option>
                              <option>Bangladesh</option>
                            </select>
                          </div>
                        </div>
                      </div>
                      <div className="col-lg-12">
                        <p className="single-form-row">
                          <label>
                            Street address <span className="required">*</span>
                          </label>
                          <input
                            type="text"
                            placeholder="House number and street name"
                            name="address"
                          />
                        </p>
                      </div>
                      <div className="col-lg-12">
                        <p className="single-form-row">
                          <input
                            type="text"
                            placeholder="Apartment, suite, unit etc. (optional)"
                            name="address"
                          />
                        </p>
                      </div>
                      <div className="col-lg-12">
                        <p className="single-form-row">
                          <label>
                            Town / City <span className="required">*</span>
                          </label>
                          <input type="text" name="address" />
                        </p>
                      </div>
                      <div className="col-lg-12">
                        <p className="single-form-row">
                          <label>State / County</label>
                          <input type="text" name="address" />
                        </p>
                      </div>
                      <div className="col-lg-12">
                        <p className="single-form-row">
                          <label>
                            Postcode / ZIP <span className="required">*</span>
                          </label>
                          <input type="text" name="address" />
                        </p>
                      </div>
                      <div className="col-lg-12">
                        <p className="single-form-row">
                          <label>Phone</label>
                          <input type="text" name="address" />
                        </p>
                      </div>
                      <div className="col-lg-12">
                        <p className="single-form-row">
                          <label>
                            Email address <span className="required">*</span>
                          </label>
                          <input type="text" name="Email address " />
                        </p>
                      </div>
                      <div className="col-lg-12">
                        <div className="checkout-box-wrap">
                          <label>
                            <input type="checkbox" id="chekout-box" /> Create an
                            account?
                          </label>
                          <div className="account-create single-form-row">
                            <p>
                              Create an account by entering the information
                              below. If you are a returning customer please
                              login at the top of the page.
                            </p>
                            <label className="creat-pass">
                              Create account password <span>*</span>
                            </label>
                            <input
                              type="password"
                              autoComplete="off"
                              className="input-text"
                            />
                          </div>
                        </div>
                      </div>
                      <div className="col-lg-12">
                        <div className="checkout-box-wrap">
                          <label id="chekout-box-2">
                            <input type="checkbox" /> Ship to a different
                            address?
                          </label>
                          <div className="ship-box-info">
                            <div className="row">
                              <div className="col-lg-6">
                                <p className="single-form-row">
                                  <label>
                                    First name{" "}
                                    <span className="required">*</span>
                                  </label>
                                  <input type="text" name="First name" />
                                </p>
                              </div>
                              <div className="col-lg-6">
                                <p className="single-form-row">
                                  <label>
                                    Username or email{" "}
                                    <span className="required">*</span>
                                  </label>
                                  <input type="text" name="Last name " />
                                </p>
                              </div>
                              <div className="col-lg-12">
                                <p className="single-form-row">
                                  <label>Company name</label>
                                  <input type="text" name="email" />
                                </p>
                              </div>
                              <div className="col-lg-12">
                                <div className="single-form-row">
                                  <label>
                                    Country <span className="required">*</span>
                                  </label>
                                  <div className="nice-select wide">
                                    <select>
                                      <option>Select Country...</option>
                                      <option>Albania</option>
                                      <option>Angola</option>
                                      <option>Argentina</option>
                                      <option>Austria</option>
                                      <option>Azerbaijan</option>
                                      <option>Bangladesh</option>
                                    </select>
                                  </div>
                                </div>
                              </div>
                              <div className="col-lg-12">
                                <p className="single-form-row">
                                  <label>
                                    Street address{" "}
                                    <span className="required">*</span>
                                  </label>
                                  <input
                                    type="text"
                                    placeholder="House number and street name"
                                    name="address"
                                  />
                                </p>
                              </div>
                              <div className="col-lg-12">
                                <p className="single-form-row">
                                  <input
                                    type="text"
                                    placeholder="Apartment, suite, unit etc. (optional)"
                                    name="address"
                                  />
                                </p>
                              </div>
                              <div className="col-lg-12">
                                <p className="single-form-row">
                                  <label>
                                    Town / City{" "}
                                    <span className="required">*</span>
                                  </label>
                                  <input type="text" name="address" />
                                </p>
                              </div>
                              <div className="col-lg-12">
                                <p className="single-form-row">
                                  <label>State / County</label>
                                  <input type="text" name="address" />
                                </p>
                              </div>
                              <div className="col-lg-12">
                                <p className="single-form-row">
                                  <label>
                                    Postcode / ZIP{" "}
                                    <span className="required">*</span>
                                  </label>
                                  <input type="text" name="address" />
                                </p>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="col-lg-12">
                        <p className="single-form-row m-0">
                          <label>Order notes</label>
                          <textarea
                            placeholder="Notes about your order, e.g. special notes for delivery."
                            className="checkout-mess"
                            rows={2}
                            cols={5}
                            defaultValue={""}
                          />
                        </p>
                      </div>
                    </div>
                  </form>
                </div>
                {/* billing-details-wrap end */}
              </div>
              <div className="col-lg-6 col-md-6">
                {/* your-order-wrapper start */}
                <div className="your-order-wrapper">
                  <h3 className="shoping-checkboxt-title">Your Order</h3>
                  {/* your-order-wrap start*/}
                  <div className="your-order-wrap">
                    {/* your-order-table start */}
                    <div className="your-order-table table-responsive">
                      <table>
                        <thead>
                          <tr>
                            <th className="product-name">Product</th>
                            <th className="product-total">Total</th>
                          </tr>
                        </thead>
                        <tbody>
                          {getdata.length > 0 ? (
                            <>
                              {getdata.map((item) => (
                                <tr className="cart_item" key={item.id}>
                                  <td className="product-name">
                                    {item.name}
                                    <strong className="product-quantity">
                                      {" "}
                                       {item.quantity}
                                    </strong>
                                  </td>
                                  <td className="product-total">
                                    <span className="amount">
                                      {item.totalPrice}
                                    </span>
                                  </td>
                                </tr>
                              ))}
                            </>
                          ) : (
                            ""
                          )}
                        </tbody>
                        <tfoot>
                          <tr className="cart-subtotal">
                            <th>Cart Subtotal</th>
                            <td>
                              <span className="amount"> {price}</span>
                            </td>
                          </tr>
                          <tr className="shipping">
                            <th>Shipping</th>
                            <td>Free</td>
                          </tr>
                          <tr className="order-total">
                            <th>Order Total</th>
                            <td>
                              <strong>
                                <span className="amount">
                                  {discountedPrice}
                                </span>
                              </strong>
                            </td>
                          </tr>
                        </tfoot>
                      </table>
                    </div>
                    {/* your-order-table end */}
                    {/* your-order-wrap end */}
                    <div className="payment-method">
                      <div className="payment-accordion">
                        {/* ACCORDION START */}
                        <h5>Direct Bank Transfer</h5>
                        <div className="payment-content">
                          <p>
                            Make your payment directly into our bank account.
                            Please use your Order ID as the payment reference.
                            Your order wont be shipped until the funds have
                            cleared in our account.
                          </p>
                        </div>
                        {/* ACCORDION END */}
                        {/* ACCORDION START */}
                        <h5>Cheque Payment</h5>
                        <div className="payment-content">
                          <p>
                            Please send your cheque to Store Name, Store Street,
                            Store Town, Store State / County, Store Postcode.
                          </p>
                        </div>
                        {/* ACCORDION END */}
                        {/* ACCORDION START */}
                        <h5>PayPal</h5>
                        <div className="payment-content">
                          <p>
                            Pay via PayPal; you can pay with your credit card if
                            you dont have a PayPal account.
                          </p>
                        </div>
                        {/* ACCORDION END */}
                      </div>
                      <div className="order-button-payment">
                        <input type="submit" defaultValue="Place order" />
                      </div>
                    </div>
                    {/* your-order-wrapper start */}
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* checkout-details-wrapper end */}
        </div>
      </div>
      {/* main-content-wrap end */}
      <Footer />
    </div>
  );
};

export default Checkout;
