import React, { useEffect, useState } from "react";
import Header from "../components/Header";
import Footer from "../components/Footer";
import { Link } from "react-router-dom";
// import { Productlist } from "../components/Productlist";
import { Userslist } from "../components/Userstest";
import { useDispatch, useSelector } from "react-redux";
import { decQuantity, ADD, Removeitem } from "../redux/actions/action";
import Lottie from "lottie-react";
import Couponanimation from "../components/Couponanimation.json";

const Cart = () => {
  const [price, setPrice] = useState(0);

  const getdata = useSelector((state) => state.cartReducer.carts);
  // console.log(getdata, "state cart page");

  const dispatch = useDispatch();

  // count total price of the cart
  const total = () => {
    let totalPrice = 0;
    getdata?.forEach((item) => {
      totalPrice += parseFloat(item.price * item.quantity);
    });
    setPrice(totalPrice);
  };

  useEffect(() => {
    total();
  }, [getdata]);

  // actions
  // decrease quantity of an item
  const dlt = (item) => {
    dispatch(decQuantity(item));
  };

  // increase quantity
  const send = (item) => {
    console.log("send",item);
    dispatch(ADD(item));
  };
  //
  const RemoveItem = (id) => {
    // console.log(e);
    dispatch(Removeitem(id));
  };

  const [error, seterror] = useState("");
  const [discount, setDiscount] = useState("");
  const [discountedPrice, setDiscountedPrice] = useState("");
  const [couponCode, setCouponCode] = useState("");
  const [isCouponCodeValid, setIsCouponCodeValid] = useState(false);
  const [showCouponAnimation, setShowCouponAnimation] = useState(false);
  const [animationDuration, setAnimationDuration] = useState(1500);

  const [appliedcoupon, setappliedcoupon] = useState("");

  const users = Userslist;
  const promocodes = users[0].promocode;

  useEffect(() => {
    let grandTotal = parseFloat(price);
    if (isCouponCodeValid) {
      const discountAmount = (discount / 100) * price;
      setDiscountedPrice((grandTotal - discountAmount).toFixed(3));
    } else {
      setDiscountedPrice(grandTotal.toFixed(3));
    }
  }, [price, isCouponCodeValid, discount]);

  const checkPromoCode = (event) => {
    event.preventDefault();
    setappliedcoupon("");
    seterror("");
    const isPromoCodeValid = promocodes.find((el) => el.code === couponCode);
    if (isPromoCodeValid) {
      setIsCouponCodeValid(true);
      setDiscount(isPromoCodeValid.discount);
      setappliedcoupon(isPromoCodeValid.code);
      setShowCouponAnimation(true);
      setTimeout(() => {
        setShowCouponAnimation(false);
      }, animationDuration);
    } else {
      seterror("Coupon not valid");
    }
  };

  const removeCouponCode = () => {
    let grandTotal = parseFloat(price);
    setDiscountedPrice(grandTotal.toFixed(3));
    setIsCouponCodeValid(false);
    setDiscount("");
    setappliedcoupon("");
    setCouponCode("");
  };

  useEffect(() => {
    console.log("remove", discount, isCouponCodeValid, appliedcoupon);
  }, [discount, isCouponCodeValid, appliedcoupon]);

  return (
    <div>
      <Header />
      {/* breadcrumb-area start */}
      <div className="cart breadcrumb-area section-ptb">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h2 className="breadcrumb-title">Cart</h2>
              {/* breadcrumb-list start */}
              <ul className="breadcrumb-list">
                <li className="breadcrumb-item">
                  <Link to="/">Home</Link>
                </li>
                <li className="breadcrumb-item active">Cart</li>
              </ul>
              {/* breadcrumb-list end */}
            </div>
          </div>
        </div>
      </div>
      {/* breadcrumb-area end */}
      {/* main-content-wrap start */}

      <div className="main-content-wrap section-ptb cart-page">
        <div className="container">
          <div className="row">
            <div className="col-12">
              {getdata.length > 0 ? (
                <div className="cart-table">
                  <div className="table-content table-responsive">
                    <table className="table">
                      <thead>
                        <tr>
                          <th className="plantmore-product-thumbnail">
                            Images
                          </th>
                          <th className="cart-product-name">Product</th>
                          <th className="plantmore-product-price">
                            Unit Price
                          </th>
                          <th className="plantmore-product-quantity">
                            Quantity
                          </th>
                          <th className="plantmore-product-subtotal">Total</th>
                          <th className="plantmore-product-remove">Remove</th>
                        </tr>
                      </thead>
                      <tbody>
                        {getdata.map((item, index) => (
                          <tr className="cart-list-item" key={index}>
                            <td className="plantmore-product-thumbnail">
                              <Link to={`/productpage/${item._id}`}>
                                <img
                                  src={`http://localhost:8000/uploads/products/${item.images[0]}`}
                                  alt="productimg"
                                />
                              </Link>
                            </td>
                            <td className="plantmore-product-name">
                              <Link to={`/productpage/${item._id}`}>
                                {item.name}
                              </Link>
                            </td>
                            <td className="plantmore-product-price">
                              <span className="amount">{item.price}</span>
                            </td>
                            <td className="plantmore-product-quantity">
                              <div className="d-flex align-items-center justify-content-center">
                                <button
                                  onClick={() => dlt(item)}
                                  className="btn m-1"
                                >
                                  -
                                </button>
                                <b className="border rounded">
                                  {item.quantity}
                                </b>
                                <button
                                  onClick={() => {
                                    console.log("send");
                                    send(item);
                                  }}
                                  className="btn m-1"
                                >
                                  +
                                </button>
                              </div>
                            </td>
                            <td className="product-subtotal">
                              <span className="amount">{item.totalPrice}</span>
                            </td>
                            <td className="plantmore-product-remove">
                              <Link
                                onClick={() => {
                                  RemoveItem(item);
                                }}
                                to="#"
                              >
                                <i className="ion-close" />
                              </Link>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                  <div className="row">
                    <div className="col-md-8">
                      <div className="coupon-all">
                        <div className="coupon2">
                          {/* <input
                            className="submit btn"
                            name="update_cart"
                            defaultValue="Update cart"
                            type="submit"
                          /> */}
                          <Link
                            to="/collections/allproducts"
                            className="btn continue-btn"
                          >
                            Continue Shopping
                          </Link>
                        </div>
                        <div>
                          <b>Apply Discount Coupon</b>
                          <form
                            onSubmit={checkPromoCode}
                            className="discount d-flex align-items-center gap-1 mb-3"
                          >
                            <div className="">
                              <input
                                type="text"
                                className="form-control"
                                placeholder="Coupon Code"
                                aria-label="Recipient's username"
                                aria-describedby="basic-addon2"
                                value={couponCode}
                                onChange={(e) => setCouponCode(e.target.value)}
                              />
                            </div>
                            <button
                              type="submit"
                              className=" btn btn-outline-secondary"
                              id="basic-addon2"
                            >
                              Apply
                            </button>
                          </form>
                          {appliedcoupon && (
                            <div className="appliedcoupon d-flex justify-content-between align-items-center">
                              <b className="text-success">
                                {" "}
                                {appliedcoupon} Coupon applied
                              </b>
                              <i
                                onClick={removeCouponCode}
                                className="fa-solid fa-xmark"
                              ></i>
                            </div>
                          )}
                          {showCouponAnimation && (
                            <div className="coupon-animation-container">
                              <div
                                className="coupon-animation"
                                // style={{ height: "100px", width: "100px" }}
                              >
                                <Lottie
                                  animationData={Couponanimation}
                                  loop={true}
                                />
                                <h3 className="text-center pb-5">
                                  Coupon applied!!
                                </h3>
                              </div>
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                    <div className="col-md-4 ml-auto">
                      <div className="cart-page-total">
                        <h2>Cart totals</h2>
                        <ul>
                          <li>
                            Subtotal <span>{price}</span>
                          </li>
                          <li>
                            Total <span>{discountedPrice}</span>
                          </li>
                        </ul>
                        {getdata.length > 0 && (
                          <Link to="/checkout" className="proceed-checkout-btn">
                            Proceed to checkout
                          </Link>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              ) : (
                <div className="no-items">
                  <div>
                    <p className="text-center">NO items in your cart</p>
                  </div>
                  <div className="coupon2 d-flex justify-content-center mt-3">
                    {/* <input
                            className="submit btn"
                            name="update_cart"
                            defaultValue="Update cart"
                            type="submit"
                          /> */}
                    <Link
                      to="/collections/allproducts"
                      className="btn continue-btn"
                    >
                      Continue Shopping
                    </Link>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      {/* main-content-wrap end */}
      <Footer />
    </div>
  );
};

export default Cart;
