import { React, useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import jwt_decode from "jwt-decode";
import { useFormik } from "formik";
import * as Yup from "yup";
import Swal from "sweetalert2";

const resetschema = Yup.object().shape({
  password: Yup.string()
    .matches(/^[^\s]+$/, "Password should not have spaces")
    .matches(
      /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d@$!%*#?&]{6,}$/,
      "Password must contain at least 6 characters, one letter and one number"
    )
    .required("Password is required"),
  confirmPassword: Yup.string()
    .required()
    .oneOf([Yup.ref("password"), null], "Password must match"),
});

const initialValues = {
  password: "",
  confirmPassword: "",
};

const ResetPass = () => {
  const [email, setEmail] = useState("");
  const { id, token } = useParams();

  const [error, setError] = useState("");

  useEffect(() => {
    const decodedToken = jwt_decode(token);
    setEmail(decodedToken.email);
  }, [token]);

  console.log(id, token);
  const navigate = useNavigate();

  const { values, errors, touched, handleBlur, handleChange, handleSubmit } =
    useFormik({
      initialValues: initialValues,
      validationSchema: resetschema,
      onSubmit: async (values) => {
        console.log("values", values);
        try {
          const res = await axios.put(
            `http://localhost:8000/api/user/resetpassword/${id}/${token}`,
            values
          );
          Swal.fire({
            icon: "success",
            title: "Password reset successfully.",
          }).then(() => {
            navigate("/"); // Redirect to home page
          });
        } catch (error) {
          console.log(error, "error");
          setError(error.res.data.message);
        }
      },
    });
  console.log(errors, "error");

  // const handleSubmit = async (event) => {
  //   event.preventDefault();
  //   if (password !== confirmPassword) {
  //     console.log("Passwords do not match.");
  //     setError("Passwords not matched");
  //     return;
  //   }
  //   try {
  //     await axios.put(
  //       `http://localhost:8000/api/user/resetpassword/${id}/${token}`,
  //       {
  //         password,
  //       }
  //     );
  //     setPassword("");
  //     setError("");
  //     alert("Password reset successfully");
  //     navigate("/");
  //   } catch (error) {
  //     console.log(error, "error");
  //     setError(error.response.data.message);
  //   }
  // };

  return (
    <div className="login container ">
      <div className="loginmain rounded">
        <form className="loginform p-4" onSubmit={handleSubmit}>
          <h1 className="text-center">Reset Password</h1>
          <p>Enter a new password for - </p>
          <p>{email}</p>
          <div className="username mb-3">
            <label htmlFor="exampleInputPassword1" className="form-label">
              New Password
            </label>
            <input
              type="password"
              className="form-control"
              id="exampleInputPassword1"
              name="password"
              value={values.password}
              onChange={handleChange}
              onBlur={handleBlur}
              minLength={6}
              maxLength={16}
              required
              placeholder="Enter 6-16 characters"
            />
            {errors.password && touched.password ? (
              <p className="form-error">{errors.password}</p>
            ) : null}
          </div>
          <div className="username mb-3">
            <label htmlFor="exampleInputPassword1" className="form-label">
              Confirm new Password
            </label>
            <input
              type="password"
              className="form-control"
              id="exampleInputPassword1"
              name="confirmPassword"
              minLength={6}
              maxLength={16}
              value={values.confirmPassword}
              onChange={handleChange}
              onBlur={handleBlur}
              required
              placeholder="Confirm Password"
            />
            {errors.confirmPassword && touched.confirmPassword ? (
              <p className="form-error">{errors.confirmPassword}</p>
            ) : null}
          </div>
          {error && <p>{error}</p>}
          <button className="loginbtn btn btn-primary" type="submit">
            Reset Password
          </button>
        </form>
      </div>
    </div>
  );
};

export default ResetPass;
