import React, { useState, useEffect, useRef } from "react";
import { axiosInstance } from "../../config";
import Profile from "../pages/Profile";
import jwtdecode from "jwt-decode";

const Adminpage = () => {
  const [user, setUser] = useState({});
  const token = localStorage.getItem("admin");
  const decoded = jwtdecode(token);
  const loggedinuser = decoded.id;
  const fetchUserProfile = async () => {
    try {
      const response = await axiosInstance.get(
        `admin/view/profile/${loggedinuser}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      console.log(response.data, "response");
      setUser(response.data);
    } catch (error) {
      console.log("Failed to fetch user profile.", error);
    }
  };
  useEffect(() => {
    fetchUserProfile();
  }, []);

  const [showProfile, setShowProfile] = useState(false);

  const toggleProfile = () => {
    setShowProfile(!showProfile);
  };

  return (
    <>
      <div className="d-flex adminpage-head justify-content-between align-items-center">
        <h1 className="user-h1">
          <i className="fa-sharp fa-solid fa-home"></i> Dashboard
        </h1>
        <button
          onClick={toggleProfile}
          className="profilepic"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#collapseExample"
          aria-expanded="false"
          aria-controls="collapseExample"
        >
          <img
            src={`http://localhost:8000/uploads/admins/${user.image}`}
            alt="profile pic"
          />
        </button>
      </div>
      <hr />

      <div className="collapse" id="collapseExample">
        <div className="card card-body">
          <div className="profile-content">
            {" "}
            <Profile />
          </div>
        </div>
      </div>

      <div></div>
    </>
  );
};

export default Adminpage;
