import { useState, useEffect, useRef } from "react";
import React from "react";
import { axiosInstance } from "../../config";
import jwtdecode from "jwt-decode";

const Profile = () => {
  const [user, setUser] = useState({});
  const token = localStorage.getItem("admin");
  console.log(token);
  const decoded = jwtdecode(token);
  const loggedinuser = decoded.id;
  console.log(loggedinuser, "decoded");

  const fetchUserProfile = async () => {
    try {
      const response = await axiosInstance.get(
        `admin/view/profile/${loggedinuser}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      console.log(token);
      console.log(response.data, "response");
      setUser(response.data);
      setusername(response.data.name);
      setemail(response.data.email);
      setmobileno(response.data.mobileno);
      onImagecancel();
    } catch (error) {
      console.log("Failed to fetch user profile.", error);
    }
  };
  useEffect(() => {
    fetchUserProfile();
  }, []);

  const [username, setusername] = useState("");
  const [Email, setemail] = useState("");
  const [Mobileno, setmobileno] = useState("");
  const [error, setError] = useState("");
  //   console.log("Error:", username);

  const setUpdateImageAction = async (event) => {
    event.preventDefault();

    const formData = new FormData();
    formData.append("image", picture.pictureAsFile);

    try {
      const response = await axiosInstance.put(
        `admin/updatepicture/${loggedinuser}`,
        formData,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      const updatearticle = response.data;

      if (updatearticle) {
        console.log("Successfully updated image");
        alert("Profile updated");
        setshowupdate(false);
        onImagecancel();
        fetchUserProfile();
      } else {
        console.log("Error found");
      }
    } catch (error) {
      console.log("Error:", error);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    // Email format validation
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(Email)) {
      setError("email invalid");
      return;
    }

    try {
      const response = await axiosInstance.put(
        `admin/updateprofile/${loggedinuser}`,
        {
          name: username,
          mobileno: Mobileno,
          email: Email,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      console.log(response.data);
      alert("Profile updated");
      setshowupdate2(false);
      fetchUserProfile();
    } catch (error) {
      console.error(error);
    }
  };

  const fileInputRef = useState(null);
  const [picture, setPicture] = useState(null);
  const [showupdate, setshowupdate] = useState(false);
  const [showupdate2, setshowupdate2] = useState(false);
  const uploadPicture = (e) => {
    setPicture({
      /* contains the preview, if you want to show the picture to the user
           you can access it with this.state.currentPicture
       */
      picturePreview: URL.createObjectURL(e.target.files[0]),
      /* this contains the file we want to send */
      pictureAsFile: e.target.files[0],
    });
  };

  const onImagecancel = () => {
    fileInputRef.current.value = "";
    setPicture(null);
  };
  const profilepic = user.pic;
  return (
    <div className=" p-4">
      <div className="d-flex">
        <div className="profilepic2 m-3">
          {user.image ? (
            <img
              src={`http://localhost:8000/uploads/admins/${user.image}`}
              alt="profile pic"
            />
          ) : (
            ""
          )}
        </div>
        <div className="profiledetails">
          {!showupdate && (
            <button
              className="profilebtn btn btn-outline-info m-1"
              onClick={() => {
                setshowupdate2(false);
                setshowupdate(true);
              }}
            >
              <i class="fa-solid fa-camera"></i>
            </button>
          )}
          {!showupdate2 && (
            <button
              className="profilebtn btn btn-outline-success m-1"
              onClick={() => {
                setshowupdate(false);
                setshowupdate2(true);
              }}
            >
              <i class="fa-solid fa-pen-to-square"></i>
            </button>
          )}
          <h5 className="text-capitalize">name: {user.name}</h5>
          <h6>email: {user.email}</h6>
          <h6>mobileno: {user.mobileno}</h6>
        </div>
      </div>
      {showupdate && (
        <div className="updateprofile">
          <form className="createform  p-3 ">
            {picture && (
              <div className="d-flex">
                {picture ? (
                  <div className=" d-flex flex-column">
                    <img
                      src={picture.picturePreview}
                      alt="Preview"
                      className="previewimage2"
                    />
                  </div>
                ) : (
                  ""
                )}
                <div className="d-flex justify-content-between align-items-center">
                  <button
                    onClick={onImagecancel}
                    className="btn btn-close"
                  ></button>
                </div>
              </div>
            )}
            <div className="d-flex align-items-center border p-2 rounded gap-2">
              <div>
                <label className=" custom-file-upload rounded">
                  Choose Photo
                  <input
                    className="filebtn"
                    ref={fileInputRef}
                    type="file"
                    onChange={uploadPicture}
                    accept="image/*"
                  />
                </label>
              </div>
              <div>
                <button
                  type="submit"
                  name="upload"
                  className="btn btn-success"
                  onClick={setUpdateImageAction}
                >
                  Update
                </button>
              </div>
              <button
                onClick={() => {
                  setshowupdate(false);
                }}
                className="btn btn-close "
              ></button>
            </div>
          </form>
        </div>
      )}
      {showupdate2 && (
        <div className="updateprofile">
          <form className="createform p-3 col-4">
            <div className="mb-3 d-flex align-items-center">
              <label for="exampleInputTitle1" class="blogs create-form-label">
                Name&nbsp;:&nbsp;
              </label>
              <input
                type="text"
                name="username"
                value={username}
                onChange={(e) => {
                  setusername(e.target.value);
                }}
                class="form-control"
                id="exampleInputTitle1"
                placeholder="Title"
                aria-describedby="TitleHelp"
              />
            </div>
            <div className="mb-3 d-flex align-items-center">
              <label for="exampleInputTitle1" class="blogs create-form-label">
                Email&nbsp;:&nbsp;
              </label>
              <input
                type="email"
                name="email"
                value={Email}
                onChange={(e) => {
                  setemail(e.target.value);
                }}
                class="form-control"
                id="exampleInputTitle1"
                placeholder="email"
                aria-describedby="TitleHelp"
              />
            </div>
            <div className="mb-3 d-flex align-items-center">
              <label for="exampleInputTitle1" class="blogs create-form-label">
                Mobileno&nbsp;:&nbsp;
              </label>
              <input
                type="number"
                name="mobileno"
                value={Mobileno}
                onChange={(e) => {
                  setmobileno(e.target.value);
                }}
                class="form-control"
                id="exampleInputTitle1"
                placeholder="Mobileno"
                aria-describedby="TitleHelp"
                minLength={10}
                maxLength={10}
              />
            </div>

            <div className="d-flex justify-content-between align-items-center">
              <button
                type="submit"
                name="upload"
                onClick={handleSubmit}
                class="btn btn-success"
                id="createsubmit"
              >
                Update
              </button>
              <button
                onClick={() => {
                  setshowupdate2(false);
                  setError("");
                }}
                className="btn btn-close "
              ></button>
            </div>
            {error ? (
              <>
                <p>{error}</p>
              </>
            ) : (
              ""
            )}
          </form>
        </div>
      )}
    </div>
  );
};

export default Profile;
