import React from "react";
import Header from "../components/Header";
import Footer from "../components/Footer";
import { Link } from "react-router-dom";

const Contact = () => {
  return (
    <div>
      <Header />
      {/* breadcrumb-area start */}
      <div className="contact breadcrumb-area section-ptb">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h2 className="breadcrumb-title">Contact Us</h2>
              {/* breadcrumb-list start */}
              <ul className="breadcrumb-list">
                <li className="breadcrumb-item">
                  <Link to="/">Home</Link>
                </li>
                <li className="breadcrumb-item active">Contact Us</li>
              </ul>
              {/* breadcrumb-list end */}
            </div>
          </div>
        </div>
      </div>
      {/* breadcrumb-area end */}
      {/* Page Conttent */}
      <main className="page-content section-ptb">
        <div className="container">
          <div className="row">
            <div className="col-lg-7 col-sm-12">
              <div className="contact-form">
                <div className="contact-form-info">
                  <div className="contact-title">
                    <h3>TELL US YOUR PROJECT</h3>
                  </div>
                  <form
                    id="contact-form"
                    action="assets/php/mail.php"
                    method="post"
                  >
                    <div className="contact-page-form">
                      <div className="contact-input">
                        <div className="contact-inner">
                          <input
                            name="con_name"
                            type="text"
                            placeholder="Name *"
                          />
                        </div>
                        <div className="contact-inner">
                          <input
                            name="con_email"
                            type="email"
                            placeholder="Email *"
                          />
                        </div>
                        <div className="contact-inner">
                          <input
                            name="con_phone"
                            type="text"
                            placeholder="Phone *"
                          />
                        </div>
                        <div className="contact-inner">
                          <input
                            name="con_subject"
                            type="text"
                            placeholder="Subject *"
                          />
                        </div>
                        <div className="contact-inner contact-message">
                          <textarea
                            name="con_message"
                            placeholder="Message *"
                            defaultValue={""}
                          />
                        </div>
                      </div>
                      <div className="contact-submit-btn">
                        <button className="submit-btn col-12" type="submit">
                          Send Email
                        </button>
                        <p className="form-messege" />
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
            <div className="col-lg-5 col-sm-12">
              <div className="contact-infor">
                <div className="contact-title">
                  <h3>CONTACT US</h3>
                </div>
                <div className="contact-dec">
                  <p>
                    Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                    Ullam nam ex odio expedita, officia temporibus ipsum,
                    placeat magni quibusdam sint, atque distinctio{" "}
                  </p>
                </div>
                <div className="contact-address">
                  <ul>
                    <li>
                      <i className="zmdi zmdi-home" /> Address : 24/488
                      Kalmandapam Palakkad ,Kerala ,India
                    </li>
                    <li>
                      <i className="zmdi zmdi-email" />
                      care.customizehere@gmail.com
                    </li>
                    <li>
                      <i className="zmdi zmdi-phone" /> +91 9188363339
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
      {/*// Page Conttent */}
      <Footer />
    </div>
  );
};

export default Contact;
