import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Header from "../components/Header";
import Footer from "../components/Footer";
import jwtDecode from "jwt-decode";
import { axiosInstance } from "../config";

const Profile = () => {
  const token = localStorage.getItem("token");
  const decoded = jwtDecode(token);
  const userid = decoded.id;
  console.log(userid,"userid");

  useEffect(() => {
    const fetchUserProfile = async () => {
      try {
        const response = await axiosInstance.get(
          `admin/view/profile/${userid}`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        console.log(response.data);
        setUser(response.data);
      } catch (error) {
        console.error(error);
      }
    };

    fetchUserProfile();
  }, []);
  const [user, setUser] = useState({});

  return (
    <div>
      <Header />
      {/* breadcrumb-area start */}
      <div className="collection breadcrumb-area section-ptb">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h2 className="breadcrumb-title">My account</h2>
              {/* breadcrumb-list start */}
              <ul className="breadcrumb-list">
                <li className="breadcrumb-item">
                  <Link to="">Home</Link>
                </li>
                <li className="breadcrumb-item active">My account</li>
              </ul>
              {/* breadcrumb-list end */}
            </div>
          </div>
        </div>
      </div>
      {/* breadcrumb-area end */}
      {/* main-content-wrap start */}
      <div className="main-content-wrap section-ptb my-account-page">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="account-dashboard">
                <div className="dashboard-upper-info">
                  <div className="row align-items-center no-gutters">
                    <div className="col-lg-3 col-md-12">
                      <div className="d-single-info">
                        <p className="user-name">
                          Hello <span>{user.email}</span>
                        </p>
                        {/* <p>
                          (not yourmail@info? <Link to="#">Log Out</Link>)
                        </p> */}
                      </div>
                    </div>
                    <div className="col-lg-4 col-md-12">
                      <div className="d-single-info">
                        <p>Need Assistance? Customer service at.</p>
                        <p>care.customizehere@gmail.com</p>
                      </div>
                    </div>
                    <div className="col-lg-3 col-md-12">
                      <div className="d-single-info">
                        <p>E-mail them at </p>
                        <p>care.customizehere@gmail.com</p>
                      </div>
                    </div>
                    <div className="col-lg-2 col-md-12">
                      <div className="d-single-info text-lg-center">
                        <Link to="/cart" className="view-cart">
                          <i className="fa fa-cart-plus" />
                          &nbsp;view cart
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12 col-lg-2">
                    {/* Nav tabs */}
                    <div className="nav dashboard-list">
                      <button
                        type="button"
                        data-bs-toggle="tab"
                        data-bs-target="#dashboard"
                        className="nav-link active"
                      >
                        Dashboard
                      </button>
                      <button
                        type="button"
                        data-bs-toggle="tab"
                        data-bs-target="#orders"
                        className="nav-link"
                      >
                        Orders
                      </button>
                      <button
                        type="button"
                        data-bs-toggle="tab"
                        data-bs-target="#downloads"
                        className="nav-link"
                      >
                        Downloads
                      </button>
                      <button
                        type="button"
                        data-bs-toggle="tab"
                        data-bs-target="#address"
                        className="nav-link"
                      >
                        Addresses
                      </button>
                      <button
                        type="button"
                        data-bs-toggle="tab"
                        data-bs-target="#account-details"
                        className="nav-link"
                      >
                        Account details
                      </button>
                      <Link to="" className="nav-link">
                        logout
                      </Link>
                    </div>
                  </div>
                  <div className="col-md-12 col-lg-10">
                    {/* Tab panes */}
                    <div className="tab-content dashboard-content">
                      <div className="tab-pane active" id="dashboard">
                        <h3>Dashboard </h3>
                        <p>
                          From your account dashboard. you can easily check
                          &amp; view your <Link to="#">recent orders</Link>,
                          manage your{" "}
                          <Link to="#">shipping and billing addresses</Link> and{" "}
                          <Link to="#">
                            edit your password and account details.
                          </Link>
                        </p>
                      </div>
                      <div className="tab-pane fade" id="orders">
                        <h3>Orders</h3>
                        <div className="table-responsive">
                          <table className="table">
                            <thead>
                              <tr>
                                <th>Order</th>
                                <th>Date</th>
                                <th>Status</th>
                                <th>Total</th>
                                <th>Actions</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <td>1</td>
                                <td>May 10, 2018</td>
                                <td>Processing</td>
                                <td>$25.00 for 1 item </td>
                                <td>
                                  <Link to="" className="view">
                                    view
                                  </Link>
                                </td>
                              </tr>
                              <tr>
                                <td>2</td>
                                <td>May 10, 2018</td>
                                <td>Processing</td>
                                <td>$17.00 for 1 item </td>
                                <td>
                                  <Link to="" className="view">
                                    view
                                  </Link>
                                </td>
                              </tr>
                            </tbody>
                          </table>
                        </div>
                      </div>
                      <div className="tab-pane fade" id="downloads">
                        <h3>Downloads</h3>
                        <div className="table-responsive">
                          <table className="table">
                            <thead>
                              <tr>
                                <th>Product</th>
                                <th>Downloads</th>
                                <th>Expires</th>
                                <th>Download</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <td>Haven - Free Real Estate PSD Template</td>
                                <td>May 10, 2018</td>
                                <td>never</td>
                                <td>
                                  <Link to="#" className="view">
                                    Click Here To Download Your File
                                  </Link>
                                </td>
                              </tr>
                              <tr>
                                <td>Nevara - ecommerce html template</td>
                                <td>Sep 11, 2018</td>
                                <td>never</td>
                                <td>
                                  <Link to="#" className="view">
                                    Click Here To Download Your File
                                  </Link>
                                </td>
                              </tr>
                            </tbody>
                          </table>
                        </div>
                      </div>
                      <div className="tab-pane" id="address">
                        <p>
                          The following addresses will be used on the checkout
                          page by default.
                        </p>
                        <h4 className="billing-address">Billing address</h4>
                        <Link to="#" className="view">
                          edit
                        </Link>
                        <p className="biller-name">Johan Don</p>
                        <p>India</p>
                      </div>
                      <div className="tab-pane fade" id="account-details">
                        <h3>Account details </h3>
                        <div className="login">
                          <div className="login-form-container">
                            <div className="account-login-form">
                              <form action="#">
                                <p>
                                  Already have an account?{" "}
                                  <Link to="#">Log in instead!</Link>
                                </p>
                                <label>Social title</label>
                                <div className="input-radio">
                                  <span className="custom-radio">
                                    <input
                                      type="radio"
                                      defaultValue={1}
                                      name="id_gender"
                                    />{" "}
                                    Mr.
                                  </span>
                                  <span className="custom-radio">
                                    <input
                                      type="radio"
                                      defaultValue={1}
                                      name="id_gender"
                                    />{" "}
                                    Mrs.
                                  </span>
                                </div>
                                <div className="account-input-box">
                                  <label>First Name</label>
                                  <input type="text" name="first-name" />
                                  <label>Last Name</label>
                                  <input type="text" name="last-name" />
                                  <label>Email</label>
                                  <input type="text" name="email-name" />
                                  <label>Password</label>
                                  <input
                                    type="password"
                                    autoComplete="off"
                                    name="user-password"
                                  />
                                  <label>Birthdate</label>
                                  <input
                                    type="text"
                                    placeholder="MM/DD/YYYY"
                                    defaultValue
                                    name="birthday"
                                  />
                                </div>
                                <div className="example">
                                  (E.g.: 05/31/1970)
                                </div>
                                <div className="custom-checkbox">
                                  <input
                                    type="checkbox"
                                    defaultValue={1}
                                    name="optin"
                                  />
                                  <label>
                                    Receive offers from our partners
                                  </label>
                                </div>
                                <div className="custom-checkbox">
                                  <input
                                    type="checkbox"
                                    defaultValue={1}
                                    name="newsletter"
                                  />
                                  <label>
                                    Sign up for our newsletter
                                    <br />
                                    <em>
                                      You may unsubscribe at any moment. For
                                      that purpose, please find our contact info
                                      in the legal notice.
                                    </em>
                                  </label>
                                </div>
                                <div className="button-box">
                                  <button
                                    className="btn default-btn"
                                    type="submit"
                                  >
                                    Save
                                  </button>
                                </div>
                              </form>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* main-content-wrap end */}
      <Footer />
    </div>
  );
};

export default Profile;
